import axios from 'axios';

const url = '/api/books/'


class BooksService {
    static getAllBooks() {
        
        return new Promise((resolve, reject) => {
            axios.get(url).then(function(res) {
                    resolve(res.data);
                }).catch(function (error) {
                    reject(error)
                })
        })

        
    }

    static getBooksDetails(bookID) {
        
        return new Promise((resolve, reject) => {
            axios.get(url + bookID).then(function(res) {
                    const resData = res.data;
                    resolve(resData);
                }).catch(function (error) {
                    reject(error)
                })
        })

        
    }
}

export default BooksService;